import React from 'react'
import Slider from '@mui/material/Slider'

import './ParamSlider.css'

export function ParamSlider({label, value, changeValue}) {
    const handleChange = (e) => {
        changeValue(e.target.value)
    }

    const valuetext = (value) => value

    return (
        <div className='param-slider-container'>
            <Slider valueLabelDisplay="auto" getAriaValueText={valuetext} aria-label={label} defaultValue={0.5} value={value} onChange={handleChange} step={0.05} min={0.1} max={1} marks />
        </div>
    )
}