import algoliasearch from "algoliasearch/lite";
import React, { useRef, useState, useEffect } from "react";
import {
  Configure,
  Hits,
  HitsPerPage,
  InstantSearch,
  Pagination,
  RefinementList,
  HierarchicalMenu,
  SearchBox,
  // SortBy,
  ToggleRefinement,
  Highlight,
  Snippet
} from "react-instantsearch-hooks-web";

import {
  AlgoliaSvg,
  ClearFilters,
  ClearFiltersMobile,
  NoResults,
  NoResultsBoundary,
  Panel,
  PriceSlider,
  Ratings,
  ResultsNumberMobile,
  SaveFiltersMobile,
  IndexSwitcher,
  ParamSlider
} from "./components";

import "./Theme.css";
import "./App.css";
import "./components/Pagination.css";
import "./App.mobile.css";
import { formatNumber } from "./utils";
import getRouting from "./routing";
import { ScrollTo } from "./components/ScrollTo";
import { Hit as AlgoliaHit } from "instantsearch.js";

import Button from "@mui/material/Button";

const searchClient = algoliasearch(
  "2HQ2D0OKD9",
  "f395f2a16c8e26dafb7ca7fc72f9c942"
);

export function App() {
  const [neuralSearchAlpha, setNeuralSearchAlpha] = useState(0.5);

  const indexNameoptions = [
    { value: "productSku-keyword", label: "Keyword" },
    { value: "productSku", label: "Neural" }
  ];

  const [showNeuralSearchAlpha, setShowNeuralSearchAlpha] = useState(false);

  const [isNeuralOn, setIsNeuralOn] = useState(false);

  const [indexName, setIndexName] = useState(indexNameoptions[0]);

  useEffect(() => {
    if (indexName.label === "Neural" && !isNeuralOn) {
      setIsNeuralOn(true);
    } else {
      setIsNeuralOn(false);
    }
  }, [indexName]);

  const routing = getRouting(indexName.value);

  const containerRef = useRef<HTMLElement>(null);
  const headerRef = useRef(null);

  function openFilters() {
    document.body.classList.add("filtering");
    window.scrollTo(0, 0);
    window.addEventListener("keyup", onKeyUp);
    window.addEventListener("click", onClick);
  }

  function closeFilters() {
    document.body.classList.remove("filtering");
    containerRef.current!.scrollIntoView();
    window.removeEventListener("keyup", onKeyUp);
    window.removeEventListener("click", onClick);
  }

  function onKeyUp(event: { key: string }) {
    if (event.key !== "Escape") {
      return;
    }

    closeFilters();
  }

  function onClick(event: MouseEvent) {
    if (event.target !== headerRef.current) {
      return;
    }

    closeFilters();
  }

  return (
    <InstantSearch
      searchClient={searchClient}
      indexName={indexName.value}
      routing={routing}
    >
      <header
        className={"header-" + indexName.label.toLowerCase() + " " + "header"}
        ref={headerRef}
      >
        <div className="alpha-button">
          {isNeuralOn && (
            <Button
              sx={{ textTransform: "none", fontWeight: "bold" }}
              onClick={() => setShowNeuralSearchAlpha(!showNeuralSearchAlpha)}
              variant="contained"
            >
              {"\u03B1"}
            </Button>
          )}
        </div>

        <p className="header-logo">
          <img
            src="https://www.walgreens.com/images/adaptive/si/1485908_WAG_Signature_logo_RGB_750x208.png "
            alt="Walgreens"
            width="50%"
            height="50%"
          ></img>
        </p>

        <p className="header-title">Stop looking for an item — find it.</p>

        <SearchBox
          placeholder="Search by keyword or item#"
          submitIconComponent={SubmitIcon}
        />
      </header>

      <Configure
        devFeatureFlags={{ neuralSearchAlpha: neuralSearchAlpha }}
        explain={["*"]}
        getRankingInfo={true}
        attributesToSnippet={["description:10"]}
        snippetEllipsisText="…"
        // removeWordsIfNoResults="allOptional"
      />

      <ScrollTo>
        <main className="container" ref={containerRef}>
          <div className="container-wrapper">
            <section className="container-filters" onKeyUp={onKeyUp}>
              <div className="container-header">
                <h2>Filters</h2>

                <div className="clear-filters" data-layout="desktop">
                  <ClearFilters />
                </div>
                <div className="set-index" data-layout="mobile">
                  <IndexSwitcher
                    indexNameoptions={indexNameoptions}
                    indexName={indexName.value}
                    setIndexName={setIndexName}
                  />
                </div>
                <div className="clear-filters" data-layout="mobile">
                  <ResultsNumberMobile />
                </div>
              </div>

              <div className="container-body">
                <Panel header="Category">
                  <HierarchicalMenu
                    attributes={[
                      "category_tier1",
                      "category_tier2",
                      "category_tier3"
                    ]}
                    limit={5}
                    showMore={true}
                  />
                </Panel>

                <Panel header="Brands">
                  <RefinementList
                    attribute="brand_name"
                    searchable={true}
                    searchablePlaceholder="Search for brands…"
                  />
                </Panel>

                <Panel header="Product Type">
                  <RefinementList
                    attribute="product_type"
                    searchable={true}
                    searchablePlaceholder="Search for product type..."
                  />
                </Panel>
              </div>
            </section>

            <footer className="container-filters-footer" data-layout="mobile">
              <div className="container-filters-footer-button-wrapper">
                <ClearFiltersMobile containerRef={containerRef} />
              </div>

              <div className="container-filters-footer-button-wrapper">
                <SaveFiltersMobile onClick={closeFilters} />
              </div>
            </footer>
          </div>

          <section className="container-results">
            <header className="container-header container-options">
              {isNeuralOn && showNeuralSearchAlpha && (
                <ParamSlider
                  label="Neural Search Alpha"
                  value={neuralSearchAlpha}
                  changeValue={setNeuralSearchAlpha}
                />
              )}
              <IndexSwitcher
                indexNameoptions={indexNameoptions}
                indexName={indexName.value}
                setIndexName={setIndexName}
              />
              {/* <SortBy
                className="container-option"
                items={[
                  {
                    label: "Sort by featured",
                    value: { indexName },
                  },
                  {
                    label: "Price ascending",
                    value: { indexName } + "_price_asc",
                  },
                  {
                    label: "Price descending",
                    value: { indexName } + "_price_desc",
                  },
                ]}
              /> */}

              <HitsPerPage
                className="container-option"
                items={[
                  {
                    label: "16 hits per page",
                    value: 16,
                    default: true
                  },
                  {
                    label: "32 hits per page",
                    value: 32
                  },
                  {
                    label: "64 hits per page",
                    value: 64
                  }
                ]}
              />
            </header>

            <NoResultsBoundary fallback={<NoResults />}>
              <Hits hitComponent={Hit} indexName={indexName} />
            </NoResultsBoundary>

            <footer className="container-footer">
              <Pagination padding={2} showFirst={false} showLast={false} />
            </footer>
          </section>
        </main>
      </ScrollTo>

      <aside data-layout="mobile">
        <button
          className="filters-button"
          data-action="open-overlay"
          onClick={openFilters}
        >
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 14">
            <path
              d="M15 1H1l5.6 6.3v4.37L9.4 13V7.3z"
              stroke="#fff"
              strokeWidth="1.29"
              fill="none"
              fillRule="evenodd"
              strokeLinecap="round"
              strokeLinejoin="round"
            />
          </svg>
          Filters
        </button>
      </aside>
    </InstantSearch>
  );
}

function SubmitIcon() {
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width="16"
      height="16"
      viewBox="0 0 18 18"
    >
      <g
        fill="none"
        fillRule="evenodd"
        stroke="currentColor"
        strokeLinecap="round"
        strokeLinejoin="round"
        strokeWidth="1.67"
        transform="translate(1 1)"
      >
        <circle cx="7.11" cy="7.11" r="7.11" />
        <path d="M16 16l-3.87-3.87" />
      </g>
    </svg>
  );
}

type HitType = AlgoliaHit<{
  image: string;
  name: string;
  categories: string[];
  description: string;
  price: number;
  rating: string;
}>;

function Hit({ hit, indexName }: { hit: HitType; indexName: any }) {
  const [label, setLabel] = useState("");

  useEffect(() => {
    const neuralOnly =
      hit._rankingInfo.hasOwnProperty("semanticScore") &&
      !hit._rankingInfo.hasOwnProperty("keywordScore");

    const keywordOnly =
      !hit._rankingInfo.hasOwnProperty("semanticScore") &&
      hit._rankingInfo.hasOwnProperty("keywordScore");

    const combination =
      hit._rankingInfo.hasOwnProperty("semanticScore") &&
      hit._rankingInfo.hasOwnProperty("keywordScore");

    if (neuralOnly) {
      setLabel("Vector");
    } else if (keywordOnly) {
      setLabel("Keyword");
    } else if (combination) {
      setLabel("Keyword & Vector");
    } else {
      setLabel("");
    }
  }, [hit, indexName]);

  return (
    <article className="hit">
      {label !== "" && (
        <div
          className={
            label.toLowerCase().replace(/\s+/g, "").replace("&", "and") +
            " " +
            "badge"
          }
        >
          <p>{label}</p>
        </div>
      )}
      <header className="hit-image-container">
        <img
          src={hit.image_url_450_a}
          alt={hit.product_name}
          className="hit-image"
        />
      </header>

      <div className="hit-info-container">
        <p className="hit-category">
          <em>{hit.sub_brand_name}</em>
        </p>
        <p className="hit-category">{hit.product_type}</p>
        <h1>
          <Highlight
            attribute="product_name"
            highlightedTagName="mark"
            hit={hit}
          />
        </h1>
        <p className="hit-description">
          <Snippet
            attribute="description"
            highlightedTagName="mark"
            hit={hit}
          />
        </p>

        <footer>
          <p>
            <span className="hit-em">$</span>{" "}
            <strong>{formatNumber(hit.final_price)}</strong>{" "}
            <span className="hit-em hit-rating">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="8"
                height="8"
                viewBox="0 0 16 16"
              >
                <path
                  fill="#e2a400"
                  fillRule="evenodd"
                  d="M10.472 5.008L16 5.816l-4 3.896.944 5.504L8 12.616l-4.944 2.6L4 9.712 0 5.816l5.528-.808L8 0z"
                />
              </svg>{" "}
              {hit.overall_rating}
            </span>
          </p>
        </footer>
      </div>
    </article>
  );
}
